<template>
    <v-main style="display:flex;justify-content:center;">
        <div style="display:flex;justify-content:center;align-content:center;" v-if="false">
            <!-- <p v-for="(a, i) in meal_diet.datas" :key="i">{{a.MLSV_YMD}}<br/></p> -->
            <div style="display:flex;align-item:center;align-content:center;">
                <v-btn style="position:fixed;left:calc(250px + 20%);" @click="load_meal_diet_page(-1)" fab elevation="4"></v-btn>
                <v-card style="padding:65px;font-size:20px" elevation="7">
                    <!-- <input type="button" @click="load_meal_diet_page(-1)" value=" < "> -->
                    <!-- <input type="button" @click="load_meal_diet_page(1)" value=" > "> -->
                    <p v-if="meal_diet!=null" v-html="meal_diet.datas[meal_diet.date+pointer-1].MLSV_YMD+'<br/>'+meal_diet.datas[meal_diet.date+pointer-1].DDISH_NM"></p>
                </v-card>
                <v-btn style="position:fixed;right:20%;" @click="load_meal_diet_page(-1)" fab elevation="4"></v-btn>
            </div>
        </div>


        <div class="meal_diet_div">
            <v-btn @click="load_meal_diet_page(-1)" fab></v-btn>
            <v-card style="padding:65px;font-size:20px;margin:40px" elevation="7">
                <p v-if="meal_diet!=null" v-html="meal_diet.datas[meal_diet.date+pointer-1].MLSV_YMD+'<br/>'+meal_diet.datas[meal_diet.date+pointer-1].DDISH_NM"></p>
            </v-card>
            <v-btn @click="load_meal_diet_page(1)" fab></v-btn>
        </div>
        <div style="width:100%;display:flex;justify-content:center;height:auto;">
            <v-btn fab style="top:100%;"></v-btn>
        </div>
    </v-main>
</template>
<script>
export default({
    data() {
        return {
            tb: 0,
            pointer: 0
        }
    },
    methods: {
        load_meal_diet_page: function(p) {
            // console.log()
            console.error((this.meal_diet.index[0] + this.meal_diet.index[2] + 1)* 5, this.meal_diet.date+this.pointer);
            if(this.meal_diet.date+this.pointer <= 2) {
                // getMeal -1
                    this.$store.dispatch('getMealDiet',-1)
                        .then((data) => {
                            this.$store.commit('changeMealDiet', data)
                    })
            } else if((this.meal_diet.index[0] + this.meal_diet.index[2] + 1) * 5 <= this.meal_diet.date+this.pointer+2) {
                // getMeal 1
                    this.$store.dispatch('getMealDiet',1)
                        .then((data) => {
                            this.$store.commit('changeMealDiet', data)
                    })
            }
            
            this.pointer += p;
        },
        load: function(a) {
            this.$store.dispatch('getMealDiet',a)
                    .then((data) => {
                        this.$store.commit('changeMealDiet', data)
                    })
            console.log(this.meal_diet.date+this.pointer-1)
            console.error(this.meal_diet.datas[this.meal_diet.date+this.pointer-1].MLSV_YMD)
        }
    },
    computed: {
        meal_diet() {
            console.log()
            if(this.$store.state.isLogin) {
                return this.$store.state.meal_diet;
            } else {
                return null;
            }
        },
    },
    created() {
    }
})
</script>
